@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap);
h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  margin-bottom: 0px; }

.layout {
  position: relative;
  min-height: 100vh; }

.content {
  background: #feffbe;
  height: 100vh;
  padding-bottom: 6rem; }

.header {
  background: #ebd494;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .header .logo {
    display: flex;
    align-items: center;
    justify-content: center; }
    .header .logo img {
      height: 50px;
      margin: 0 20px 0 20px; }
  .header .navbar {
    display: flex;
    align-items: center; }
    .header .navbar .ant-menu {
      border-bottom: 0px;
      background: rgba(0, 0, 0, 0); }
      .header .navbar .ant-menu .ant-menu-item-active,
      .header .navbar .ant-menu .ant-menu-item-selected {
        border-color: #feffbe; }
    .header .navbar li {
      margin-left: 25px; }

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: center;
  background: #472836;
  padding-top: 0px;
  padding-bottom: 0px; }

